.field {
  height: 48px;
}

.field.small {
  height: 32px;
}

.field.middle-align {
  display: flex;
  align-items: center;
}

.field.top-align {
  display: flex;
  align-items: flex-start;
}

.field.bottom-align {
  display: flex;
  align-items: flex-end;
}

.field + .field {
  margin-top: 16px;
}

.field > label,
.field > i {
  z-index: 0;
}

.field > label:first-child {
  padding: 4px 0;
  display: block;
}

.field > i:first-child {
  position: absolute;
  top: 50%;
  left: 4px;
  transform: translateY(-50%);
  cursor: pointer;
  color: rgba(0, 0, 0, 0.5);
}

.field > i:last-child {
  position: absolute;
  top: 50%;
  right: 4px;
  transform: translateY(-50%);
  cursor: pointer;
  color: rgba(0, 0, 0, 0.5);
}

.field > input[type="text"] {
  border: 0;
  border-radius: 8px 8px 0 0;
  border-bottom: 1px solid #9e9e9e;
  padding: 20px 0 4px 0;
  font-family: Roboto;
  font-size: 0.9rem;
  width: 100%;
  height: 100%;
  outline: none;
  z-index: 1;
  background-color: transparent;
  box-sizing: border-box;
}

.field > input[type="text"]:focus {
  border-bottom: 2px solid #2196f3;
}

.field > input[type="text"] + label {
  animation: 0.1s label-to-middle;
  position: absolute;
  top: 50%;
  left: 0;
  font-size: 0.9rem;
  transform: translateY(-50%);
}

.field > input[type="text"] + label.active {
  animation: 0.1s label-to-top;
  top: 4px;
  font-size: 0.8rem;
  transform: none;
}

.field > i + input[type="text"] {
  padding: 20px 0 4px 32px;
}

.field > i + input[type="text"] + label {
  left: 32px;
}

.field > input[type="text"].border {
  border: 1px solid #9e9e9e;
  border-radius: 8px;
  padding: 20px 8px 4px 8px;
}

.field > input[type="text"].border:focus {
  border: 2px solid #2196f3;
}

.field > input[type="text"].border + label {
  left: 8px;
}

.field > i + input[type="text"].border {
  padding: 20px 8px 4px 32px;
}

.field > i + input[type="text"].border + label {
  left: 32px;
}

.field > input[type="text"].fill {
  background-color: rgba(0, 0, 0, 0.02);
  padding: 20px 8px 4px 8px;
}

.field > input[type="text"].fill + label {
  left: 8px;
}

.field > i + input[type="text"].fill {
  padding: 20px 8px 4px 32px;
}

.field > i + input[type="text"].fill + label {
  left: 32px;
}

.field > input[type="text"].round {
  border: 0;
  border-radius: 24px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12),
    0 3px 1px -2px rgba(0, 0, 0, 0.2);
  padding: 20px 16px 4px 16px;
}

.field > input[type="text"].round + label {
  left: 16px;
}

.field > i + input[type="text"].round {
  padding: 20px 16px 4px 32px;
}

.field > i + input[type="text"].round + label {
  left: 32px;
}

.field > input[type="text"].round.border {
  border: 1px solid #9e9e9e;
  border-radius: 24px;
  box-shadow: none;
}

.field > input[type="text"].round.border:focus {
  border: 2px solid #2196f3;
}

.field > input[type="text"].round.fill {
  border: 0;
  border-radius: 24px;
  box-shadow: none;
}

.field.small > input[type="text"] {
  padding: 0 8px !important;
}

.field.small > input[type="text"] + label {
  display: none;
}

.field.small > i + input[type="text"] {
  padding: 0 8px 0 32px !important;
}

.field > input[type="checkbox"] + label + label.active,
.field > input[type="radio"] + label + label.active {
  position: absolute;
  top: 4px;
  left: 0;
  font-size: 0.8rem;
  transform: none;
}

input[type="checkbox"],
input[type="radio"] {
  -webkit-appearance: none;
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
  outline: none;
  cursor: pointer;
  vertical-align: middle;
  margin: 0;
  padding: 0;
  color: rgba(0, 0, 0, 0.5);
}

input[type="checkbox"]:checked,
input[type="radio"]:checked {
  background-color: transparent;
  animation: 0.4s to-checked;
}

input[type="checkbox"]::after {
  content: "check_box_outline_blank";
}

input[type="checkbox"]:checked::after {
  color: #2196f3;
  content: "check_box";
}

input[type="radio"]::after {
  content: "radio_button_unchecked";
}

input[type="radio"]:checked::after {
  color: #2196f3;
  content: "radio_button_checked";
}

input[type="checkbox"] + label,
input[type="radio"] + label {
  margin: 0 16px 0 8px;
  vertical-align: middle;
}

.field > select {
  border: 0;
  border-radius: 8px 8px 0 0;
  border-bottom: 1px solid #9e9e9e;
  padding: 16px 0 0 0;
  font-family: Roboto;
  font-size: 0.9rem;
  width: 100%;
  height: 100%;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  box-sizing: border-box;
  -webkit-appearance: none;
}

.field > select:focus {
  border-bottom: 2px solid #2196f3;
}

.field > select + label {
  animation: 0.1s label-to-middle;
  position: absolute;
  top: 50%;
  left: 0;
  font-size: 0.9rem;
  transform: translateY(-50%);
}

.field > select + label.active {
  animation: 0.1s label-to-top;
  top: 4px;
  font-size: 0.8rem;
  transform: none;
}

.field > i + select {
  padding: 16px 0 0 32px;
}

.field > i + select + label {
  left: 32px;
}

.field > select.border {
  border: 1px solid #9e9e9e;
  border-radius: 8px;
  padding: 16px 8px 0 8px;
}

.field > select.border:focus {
  border: 2px solid #2196f3;
}

.field > select.border + label {
  left: 8px;
}

.field > i + select.border {
  padding: 16px 8px 0 32px;
}

.field > i + select.border + label {
  left: 32px;
}

.field > select.fill {
  background-color: rgba(0, 0, 0, 0.02);
  padding: 16px 8px 0 8px;
}

.field > select.fill + label {
  left: 8px;
}

.field > i + select.fill {
  padding: 16px 8px 0 32px;
}

.field > i + select.fill + label {
  left: 32px;
}

.field > select.round {
  border: 0;
  border-radius: 24px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12),
    0 3px 1px -2px rgba(0, 0, 0, 0.2);
  padding: 20px 16px 4px 16px;
}

.field > select.round + label {
  left: 16px;
}

.field > i + select.round {
  padding: 20px 16px 4px 32px;
}

.field > i + select.round + label {
  left: 32px;
}

.field > select.round.border {
  border: 1px solid #9e9e9e;
  border-radius: 24px;
  box-shadow: none;
}

.field > select.round.border:focus {
  border: 2px solid #2196f3;
}

.field > select.round.fill {
  border: 0;
  border-radius: 24px;
  box-shadow: none;
}

.field.small > select {
  padding: 0 8px !important;
}

.field.small > select + label {
  display: none;
}

.field.small > i + select {
  padding: 0 8px 0 32px !important;
}

@keyframes label-to-top {
  from {
    top: 50%;
    font-size: 0.9rem;
    transform: translateY(-50%);
  }
  to {
    top: 4px;
    font-size: 0.8rem;
    transform: none;
  }
}

@keyframes label-to-middle {
  from {
    top: 4px;
    font-size: 0.8rem;
    transform: none;
  }
  to {
    top: 50%;
    font-size: 0.9rem;
    transform: translateY(-50%);
  }
}

@keyframes to-checked {
  from {
    border-radius: 50%;
    background-color: rgba(52, 172, 224, 1);
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 1);
  }
  to {
    border-radius: 50%;
    background-color: rgba(52, 172, 224, 0);
    box-shadow: 0 0 0 8px rgba(52, 172, 224, 0);
  }
}
